#loader {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;
    z-index: 99999999;
    width: 100%;
    height: 100vh;
    background-color: #111;
    color: white;
}

#loader #topheading {
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translate(-50%, 0);
}

#topheading h5 {
    text-align: center;
    text-transform: uppercase;
    font-size: 11px;
    font-weight: 300;
}

#loader h1 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    font-size: 4vw;
    font-weight: 500;
}

#surname {
    color: #14CF93;
}

#green {
    position: absolute;
    width: 100%;
    bottom: 0;
    height: 0px;
    background-color: #14CF93;
}

#white {
    position: absolute;
    width: 100%;
    bottom: 0;
    height: 0;
    background-color: #dadada;
}

.reveal .tparent {
    display: block;
    overflow: hidden;
}

.reveal .tparent .tchild {
    display: block;
}

.reveal .tparent .tchild span {
    display: inline-block;
}

.revealNew .tparentNew {
    display: block;
    overflow: hidden;
}

.revealNew .tparentNew .tchildNew {
    display: block;
}

.revealNew .tparentNew .tchildNew span {
    display: inline-block;
}